import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

export let store = new Vuex.Store({
  state: {
    이름 : 'John',
    나이 : 20,
  },
  getters : {
    이름꺼내기(state){
      return state.이름
    }
  },
  mutations: {
    나이한살더하기( state ) {
      state.나이++
    },
    이름바꾸기( state, 데이터 ){
      state.이름 = 데이터
    }
  },
  actions : {
    // 이름바꾸기(context){
    //   axios.get().then(결과=>{
    //     context.commit('이름바꾸기', 결과);
    //   })
    // }
    //$store.dispatch('이름바꾸기');
    //1. ajax 요청을 통해서 mutations를 해야하는 경우
  }  
})
